.footer-container{
     display: flex;
    flex-direction: column; 
    
    position: relative;
}
.footer-container>hr{
  
border: 1px solid var(--lightgray);
width: 100vw;
}
.footer{
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    align-items: center;
    justify-content: center;
    height:20rem;
}
.social-links{
    display: flex;
    gap: 4rem;
}
.social-links>img{
    width: 2rem;
    height: 2rem;
    cursor: pointer;

}
.footer-logo>img{
    width: 10rem;
     
}
.footer-blur-1{
   bottom: 0;
   right: 15%;
   width: 26rem;
   height: 12rem; 
   filter: blur(200px);
   background:rgb(255, 115, 0)
  
}
.footer-blur-2{
    bottom: 0;
    left: 15%;
    width: 26rem;
    height: 12rem;
    filter: blur(200px);
    background:rgb(255, 115, 0)
}

    