.hero{
    display: flex ;
    justify-content: space-between;
}
.left-hero{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.right-hero{
    flex: 1;
    position: relative;
    background-color: var(--orange);
}
.the-best-ad{
    background-color: #363d42;
    margin-top: 4rem;
    border-radius: 4rem;
    padding: 20px;
    color: white;
    width: fit-content;
    text-transform:uppercase;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.the-best-ad span{
    z-index: 2;
}
.the-best-ad div{
    position: absolute;
    left: 8px;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    border-radius: 3rem;
    z-index: 1;
}
.hero-text{
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 4.5rem;
    font-weight: 800;
    text-transform: uppercase;
    gap: 1.5rem;
    text-overflow: inherit;
}
.hero-text div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
}
.figures{
    display: flex;
    gap: 6rem;
}
.figures div{
    display: flex;
    flex-direction: column;
}
.figures div span:nth-of-type(1){
color: white;
font-size: 2rem;
}
.figures div span:nth-of-type(2){
    color:grey;
    text-transform: uppercase;
}
.hero-buttons{
    display: flex;
    gap:1rem;
    font-weight: normal;
}
.hero-buttons button:nth-of-type(1){
    width: 8rem;
    color: white;
    background-color: var(--orange);
}
.hero-buttons button:nth-of-type(2){
    width: 8rem;
    color: white;
    background-color: transparent;
    border: 2px solid var(--orange)
}
.right-hero .btn{
    position: absolute;
    right: 3px;
    top: 2px;
    color:black;
}
.heart-rate{
    display: flex;
    flex-direction: column;
    gap:1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    align-items: start;
    padding: 1rem;
    border-radius: 5px;
    position: absolute;
    top:7rem;
    right:4rem;

}
.heart-rate img{
    width: 2rem;
}
.heart-rate :nth-child(2)
{
color: var(--gray);
text-transform: capitalize;
}
.heart-rate :nth-child(3)
{
    color: white;
    font-size: 1.5rem;
}
.hero-img{
    position: absolute;
    right: 8rem;
    top: 10rem;
    width: 23rem;

}
.hero-img-back{
    position: absolute;
    top: 4rem;
    right: 20rem;
    z-index: -1;
    width: 15rem;
}
.calories{
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top: 32rem;
    right: 28rem;
}
.calories img{
    width: 3rem;
}
.calories div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.calories div :nth-child(1)
{
    color:var(--gray)
}
.calories div :nth-child(2)
{
    color:white;
    font-size: 1.5rem;
}
.hero-blur{
width: 22rem;
height: 30rem;
left: 0;
}

@media screen and (max-width:768px)
{
   .hero{
    flex-direction: column;} 
    .hero-blur{
        width: 14rem;
    }
    .the-best-ad{
        margin-top: 0;
        font-size: small;
        align-self: center;
        transform: scale(0.8);
    }
    .hero-text{
        font-size: xx-large;
        align-items: center;
        justify-content: center;
    }
    .hero-text>div:nth-of-type(3)
    {
     font-size: small;
     font-weight: 200;
     letter-spacing: 1px;
     text-align: center;
    }
    .hero-buttons{
        justify-content: center;
    }
    .figures{
     flex-direction: row;
     gap: 4rem;
    }
    .figures>div>span:nth-of-type(1){
        font-size: small;

    }
    .figures>div>span:nth-of-type(2){
        font-size: small;

    }
    .figures>div>span:nth-of-type(3){
        font-size: small;

    }
    .right-hero{
        position: relative;
        background:none;
    }
    .heart-rate{
        left: 1rem;
        top: 2rem;
    }
    .calories{
        position: relative;
        left: 2rem;
        top: 5rem;
    }
    .calories>img{
        width: 2rem;
    }
    .calories>div>:nth-child(2){
        color: white;
        font-size: 1rem;
    }
    .hero-img{
        position: relative;
        width: 15rem;
        left: 7rem;
        top: 4rem;
        align-self: center;
    }
    .hero-img-back{
        width: 15rem;
        left: 2rem;
        top: 0rem;
    }
    

}